<template>
  <div>
    <div id="title">三次贝塞尔曲线（拖动）</div>
    <div id="all">
      <div id="left" ref="scope">
        <canvas id="canvasbz" ref="draw">你的浏览器不支持canvas</canvas>
        <div id="one" ref="pointOne" @mousedown="changePoint($event,pointOne,1)">{{Data.num[0]}}</div>
        <div id="two" ref="pointTwo" @mousedown="changePoint($event,pointTwo,2)">{{Data.num[1]}}</div>
        <div id="three" ref="pointThree" @mousedown="changePoint($event,pointThree,3)">{{Data.num[2]}}</div>
        <div id="four" ref="pointFour" @mousedown="changePoint($event,pointFour,4)">{{Data.num[3]}}</div>
      </div>
      <div id="right">
        代码<br><hr><br>
        {{Data.code}}<br><br><hr><br>
        <p>本工具主要作用是为绘制三次贝塞尔曲线<mark>作参考</mark></p>
        <br><hr><br>
        辅助选项：
        辅助线<input type="checkbox" v-model="Data.isShowAuxiliaryLine">
        点名<input type="checkbox" v-model="Data.isShowAuxiliaryPoint">
        吸附<input type="checkbox" v-model="Data.isAdsorption">
        <br><br><hr><br>
        <button class="butt" @click="flash">刷新</button><br>（勾选辅助选项点击刷新之后就可以实现）
      </div>
    </div>
  </div>
</template>
<script setup>
import {ref,onMounted, reactive} from "vue"
  const draw=ref(null)
  const scope=ref(null)
  const pointOne=ref(null)
  const pointTwo=ref(null)
  const pointThree=ref(null)
  const pointFour=ref(null)

  let Data=reactive({
    where:[
    {
      x:5,
      y:5
    },{
      x:995,
      y:5
    },{
      x:5,
      y:445
    },{
      x:995,
      y:445
    }
    ],
    code:`
      c.beginPath();
      c.lineWidth=5;
      c.strokeStyle="red";
      c.moveTo(0,0);
      c.bezierCurveTo(990,0,0,440,990,440);
      c.stroke();
      c.closePath();
      `
    ,
    c:null,
    drawWhere:{
      x:0,
      y:0
    },
    isAdsorption:false,
    adsorptionText:'自由状态',
    isShowAuxiliaryLine:true,
    isShowAuxiliaryPoint:false,
    num:['','','','']
  })

  onMounted(()=>{/*挂载完毕后执行*/
    draw.value.width=scope.value.clientWidth
    draw.value.height=scope.value.clientHeight
    console.log(draw.value.id);
    Data.c = draw.value.getContext('2d')//获取笔刷
    drawBezier(Data.c,Data.where)
    drawLine(Data.c,Data.where)
    changeCode()
  })

  const clearAll=function(c){/*清屏*/
    c.clearRect(0, 0, draw.value.width, draw.value.height)
  }

  const drawBezier=function(c,where){/*绘制曲线*/
    c.beginPath()
    c.save()

    c.lineWidth=5
    c.strokeStyle="rgb(209, 83, 83)"
    c.moveTo(Data.where[0].x,Data.where[0].y)
    c.bezierCurveTo(Data.where[1].x,Data.where[1].y,Data.where[2].x,Data.where[2].y,Data.where[3].x,Data.where[3].y)
    c.stroke()

    c.restore()
    c.closePath()
  }

  const drawLine=function(c,where){/*绘制线条*/
      c.beginPath()
      c.lineCap='round'
      c.strokeStyle="rgb(150, 150, 150)"
      c.moveTo(Data.where[0].x,Data.where[0].y)
      c.lineTo(Data.where[1].x,Data.where[1].y)
      c.stroke()
      c.closePath()

      c.beginPath()
      c.moveTo(Data.where[2].x,Data.where[2].y)
      c.lineTo(Data.where[3].x,Data.where[3].y)
      c.stroke()
      c.closePath()
  }
  const changePoint=function(e,r,num){/*改变点位*/

    let l = e.clientX - r.offsetLeft
    let t = e.clientY - r.offsetTop
    document.onmousemove = function(e){
      /*1rem=10px*/
      Data.x = e.clientX - l
      Data.y = e.clientY - t
      if(Data.x+25<=5){
        Data.x=-20
      }
      if(Data.x+25>=995){
        Data.x=970
      }
      if(Data.y+25<=5){
        Data.y=-20
      }
      if(Data.y+25>=445){
        Data.y=420
      }
      if(Data.isAdsorption){
        Data.x=parseInt(Data.x/5)*5
        Data.y=parseInt(Data.y/5)*5
      }
      if(!Data.isShowAuxiliaryPoint){
        Data.num=['','','','']
      }else{
        Data.num=['A','B','C','D']
      }
      r.style.left = Data.x + "px"
      r.style.top = Data.y + "px"
      Data.where[num-1].x=Data.x+25
      Data.where[num-1].y=Data.y+25
      clearAll(Data.c)
      drawBezier(Data.c,Data.where)
      if(Data.isShowAuxiliaryLine){
      drawLine(Data.c,Data.where)
      }
      changeCode()

    }
    document.onmouseup = function (e) {
      document.onmousemove = null
      document.onmouseup = null
    }
  }

  const changeCode =function(){/*修改代码文本*/
    Data.code=`
    c.beginPath();
    c.lineWidth=5;
    c.strokeStyle="red";
    c.moveTo(${Data.where[0].x-5},${Data.where[0].y-5});
    c.bezierCurveTo(${Data.where[1].x-5},${Data.where[1].y-5},${Data.where[2].x-5},${Data.where[2].y-5},${Data.where[3].x-5},${Data.where[3].y-5});
    c.stroke();
    c.closePath();`
  }
  const flash=function(){
    if(!Data.isShowAuxiliaryPoint){
      Data.num=['','','','']
    }else{
      Data.num=['A','B','C','D']
    }
    clearAll(Data.c)
    drawBezier(Data.c,Data.where)
    if(Data.isShowAuxiliaryLine){
    drawLine(Data.c,Data.where)
    }
  }



</script>

<style scoped>
#title{
  text-align: center;
  height: 5rem;
  line-height: 5rem;
  font-size: 3rem;
  font-weight: bold;
}
#all{
  height: 50rem;
  position: relative;
}
#left{
  height: 45rem;
  width: 100%;
  border-radius: 1rem;
  position: relative;
  user-select: none !important;
}
#right{
  height: 44rem;
  position: absolute;
  left: 100%;
  top:0;
  width: 30%;
  border-bottom-right-radius: 1rem;
  overflow-y: auto;
  text-align: left;
  margin: 0 5rem;
  padding: 1rem 1rem 0 1rem;
  border-radius: 1rem;
  border: solid 1px rgb(255, 255, 255);
  color: white;
  background-color: rgba(0, 0, 0, 0.397);
}
#one,#two,#three,#four{
  width: 5rem;
  height: 5rem;
  background-color: rgba(207, 109, 109, 0.288);
  position: absolute;
  border-radius: 50%;
  top: -20px;
  left: -20px;
  line-height: 8rem;
  cursor:move;

}

#two{
  top: -20px;
  left: 970px;
}
#three{
  top: 420px;
  left: -20px;
}
#four{
  top: 420px;
  left: 970px
}
#input{
  margin: 1rem 0;
}
.butt{
  width: 10rem;
  height: 4rem;
  margin: 1rem;
  border-radius: 2rem;
  background-color: white;
  color: black;
  border:black 2px solid;
}
.butt:hover{
  background-color: black;
  color: white;
}
.butt:active{
  background-color: white;
}
</style>