<template>
    <div>
    <div id="title">颜色混合（中性）</div>
    <div id="all">
      <div id="left" ref="colorMix">
        <canvas id="canvasColor" ref="colorMixDraw">你的浏览器不支持canvas</canvas>
      </div>
      <div id="right">
        输入分段数<input class="input" type="number" max=10000 v-model="Data.n">
        <button class="butt" @click="zeroV">置零</button>
        <button class="butt" @click="onePiV">1π速度</button>
        <button class="butt" @click="addV">加速</button>
        <button class="butt" @click="stop">停止</button>
        <button class="butt" @click="restart">旋转</button>
        <br>
        当前速度：每帧：{{parseFloat(Data.v.toExponential(1))}}π
        <br>
        {{Data.running}}
      </div>
    </div>
  </div>
</template>

<script setup>
import {ref,onMounted,beforeDestroy,mgetCurrentInstance, reactive, watch} from "vue"
let colorMixDraw=ref(null)
let colorMix=ref(null)
let Data=reactive({
  c:null,
  c2:null,
  x:50,
  v:0.1,
  Color:['red','blue'],
  n:2,
  run:0,
  running:'旋转中...'
  })
onMounted(()=>{/*挂载完毕后执行*/
    colorMixDraw.value.width=colorMix.value.clientWidth
    colorMixDraw.value.height=colorMix.value.clientHeight
    Data.c = canvasColor.getContext('2d')//获取笔刷
    Data.c2= canvasColor.getContext('2d')//获取笔刷
    Data.c.translate(300,200)
    runIt()
  })

const draw=function(){
  if(Data.n==''){
    Data.n=2
  }
  if(Data.n>9999){
    Data.n=10000
  }
  let deg=0
  Data.c.rotate(Math.PI*Data.v)
  for(let i=0;i<parseInt(Data.n);){
    Data.c.beginPath();
    Data.c.moveTo(0,0)
    Data.c.arc(0,0,200,deg,(i+parseInt(1))*2*Math.PI*(1/Data.n));
    Data.c.closePath();
    Data.c.fillStyle=Data.Color[i%2]
    // console.log(i)
    // console.log(Data.Color[i])
    Data.c.fill()
    deg=(i+parseInt(1))*2*Math.PI*(1/Data.n)
    i++
  }
}
const runIt=function(){
  Data.run=window.requestAnimationFrame(runIt)
  draw()
}
const addV=function(){
  Data.v=Data.v+0.1
}
const zeroV=function(){
  Data.v=0
}
const onePiV=function(){
  Data.v=1
}
const stop=function(){
  cancelAnimationFrame(Data.run)
  Data.running='停止了'
}
const restart=function(){
  cancelAnimationFrame(Data.run)
  Data.running='旋转中...'
  runIt()
}
</script>

<style scoped>
#title{
  text-align: center;
  height: 5rem;
  line-height: 5rem;
  font-size: 3rem;
  font-weight: bold;
}
#all{
  height: 50rem;
  display: flex;
}
#left{
  width: 80%;
}
#right{
  width: 20%;
}
.input{
  width: 5rem;
  height: 3rem;
  margin: 1rem;
  -moz-appearance: textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0;
}
.butt{
  width: 10rem;
  height: 4rem;
  margin: 1rem auto;
  border-radius: 2rem;
  background-color: white;
  color: black;
  border:black 2px solid;
}
.butt:hover{
  background-color: black;
  color: white;
}
.butt:active{
  background-color: white;
}
</style>